<template>
    <v-row class="fill-height my-5" justify="center">
        <v-card variant="text" class="pa-1" max-width="1000">
            <v-card-title class="d-flex align-start pe-2 text-h6" style="width: 1000px;">
                <v-icon>mdi-view-dashboard</v-icon>
                프로젝트 소개
            </v-card-title>
            <v-row>
                <v-col
                    v-for="item in info"
                    :key="item.id"
                    cols="12" sm="6" md="4"
                >
                    <v-card>
                        <v-skeleton-loader type="image, heading, text, actions">
                            <v-responsive>
                                <v-img max-height="124" min-height="124" :src="item.path" />
                                <v-card-title class="d-flex align-center">
                                    <h5>{{ item.name }}</h5>
                                </v-card-title>
                                <v-card-text>
                                    <div :class="['clamp-2', { expanded: isExpanded[item.id] }]">
                                        {{ item.subject }}
                                    </div>
                                    <v-card-actions class="d-flex justify-end">
                                        <v-btn
                                            variant="text"
                                            size="small"
                                            class="mt-1 px-0"
                                            :icon="!isExpanded[item.id] ? 'mdi-dots-horizontal' : 'mdi-minus'"
                                            @click="toggleExpand(item.id)"
                                        >
                                        </v-btn>
                                    </v-card-actions>
                                </v-card-text>
                                <v-card-actions class="d-flex justify-end">
                                    <v-btn 
                                        variant="text"
                                        :to="{ name: 'projectDetails', query: { id: item.id } }"
                                    >
                                        <v-tooltip
                                            activator="parent"
                                            location="bottom"
                                        >자세히</v-tooltip>
                                        <v-icon>mdi-magnify</v-icon>
                                    </v-btn>
                                </v-card-actions>
                            </v-responsive>
                        </v-skeleton-loader>
                    </v-card>
                </v-col>
            </v-row>
        </v-card>
    </v-row>
</template>
<script>
import fmresman from '@/assets/project-img/fmresman.png'
import business from '@/assets/project-img/business.png'
import dolbom from '@/assets/project-img/dolbom-1.png'
import cb from '@/assets/project-img/cb-1.png'
import easystem from '@/assets/project-img/ea-login_1.png'
import system from '@/assets/project-img/system.png'
import toji from '@/assets/project-img/toji.png'
export default {
    name: "ProjectListView",
    data() {
        return {
            isExpanded: {},
            info: [
                {   
                    id: 1,
                    name: "당진 시설관리 교육문화 스포츠센터",
                    subject: "당진시 스포츠센터 및 평생학습 등 시설을 예약하고 결제를 관리하며"
                     + " 관리자는 성별, 나이, 반에 따라서 일, 월, 연도 별로 결제 금액 확인이 가능한 플랫폼입니다.",
                    path: fmresman,
                    to: '/project-details'
                },
                { 
                    id: 2,  
                    name: "당진시 EA 시스템",
                    subject: "당진시 서버 자원관리를 좀 더 효율적으로 관리하고 서버 담당자, 담당 주무관 등 History를 "
                    + "확인하여 유지관리에 도움을 주고자 진행된 사업입니다.",
                    path: easystem,
                    to: '/project-details'
                },
                {   
                    id: 3,
                    name: "국가기록원",
                    subject: "국가기록원에 저장되어야 하는 대학관리 지침 문서들을 웹 에디터를 사용하여 저장 후 "
                    + "코드화를 진행하여 사용자에게 문서 내용의 원본을 최대한 그대로 표출해주는 플랫폼입니다.",
                    path: system,
                    to: '/project-details'
                },
                {   
                    id: 4,
                    name: "돌봄 플랫폼",
                    subject: "학교 수업 방과 후 부모의 양육 공백 발생 시 신청 접수하여 각 센터마다 배치되어 "
                    + "돌봄 서비스를 받는 플랫폼입니다.",
                    path: dolbom,
                    to: '/project-details'
                },
                {   
                    id: 5,
                    name: "충북 출산육아 수당 시스템",
                    subject: "충북 시, 군, 구 지역에서 출산 및 양육 지원에 문서화를 간편하고 전산화하고자 하여 "
                    + "진행되었던 프로젝트입니다.",
                    path: cb,
                    to: '/project-details'
                },
                {   
                    id: 6,
                    name: "당진 토지관리 통합 플랫폼",
                    subject: "처음 시작 시 간단한 토지 지적 정보 입력 확인 플랫폼이였으나 토지 정보에 관련하여 "
                    + "프로젝트가 추가되었으며 당진시 토지과 관련 기능을 통합하여 생성한 플랫폼입니다.",
                    path: toji,
                    to: '/project-details'
                },
                {   
                    id: 7,
                    name: "정선 업무관리 시스템",
                    subject: "부서 업무, 부서 업무 하위에 존재하는 개인 업무를 입력하고 각 업무 담당자를 지정하여 "
                    + "개인 업무 완료 처리 시 부서 업무 진척률, 부서 업무 친척률에 따라서 해당 부서 업무 진척률을 표기하고 "
                    + "이에 따라 군수가 진척률을 확인하기 용이하도록 진행되었던 프로젝트입니다.",
                    path: business,
                    to: '/project-details'
                },
            ]
        }
    },
    methods: {
        truncate(text, length = 50) {
            if(!text) return ''
            return text.length > length ? text.substring(0, length) + '...' : text
        },
        toggleExpand(id) {
            this.isExpanded[id] = !this.isExpanded[id]
        }
    }
}
</script>

<style>
    .clamp-2 {
        display: -webkit-box;
        -webkit-line-clamp: 2; /* 두 줄 */
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    .expanded {
        -webkit-line-clamp: unset; /* 제한 해제 */
    }
</style>